import "../../styles.scss";
import SideItem from "components/atom/sideItem/SideItem";
import { Images } from "shared/images";
import { navbarData_Admin } from "./navbarData";
import { useHistory } from "react-router-dom";
import { ROUTE_CONSTANTS } from "shared/routeConstants";
import { toggleClasses } from 'components/utility';
import { useCallback, useEffect, useState } from "react";

function NavBarHOC(props) {
  const history = useHistory();
  const [active, setActive] = useState(0);

  const handleItemClick = (data) => {
    setActive(data.id)
    history.push(data.path);
  }
  

  return (
    <>
      <aside className="sideBar">
        <button className ="d-lg-none close_btn"><img src={Images.CloseIcon} alt="Close" width="30" /></button>
        <nav className ="navbar text-left">
          <div className="navbar-brand" onClick={()=> history.push(ROUTE_CONSTANTS.DASHBOARD)}>
            <img src={Images.locumLogo} alt="locumlogo" width="194" className="img-fluid" />
          </div>
          <ul className="navbar-nav w-100">
            {navbarData_Admin.map((item,idx) => {
              return <SideItem key={item.id} data={item} handleClick={handleItemClick} active={active}/>;
            })}
          </ul>
        </nav>
      </aside>
    </>
  );
}

export default NavBarHOC;
