import React, { useCallback, useState } from "react";
import "./styles.scss";
import SearchBar from "components/atom/searchBar";
import CustomTable from "components/atom/table";
import UserTable from "./Table";
import { Pagination } from "components/atom/pagination";
import ServicesTable from "./Table";
import { ROUTE_TITLES } from "shared/routeConstants";
import { startLoader } from "redux/actions/commonCRUD";
import { useDispatch } from "react-redux";
import {
  pharmacyServiceAdd,
  pharmacyServiceDelete,
  pharmacyServiceEdit,
  pharmacyServiceListing,
  pharmacyServiceStatusChange,
} from "redux/actions/auth";
import {
  API_RESPONSE,
  BUTTONS,
  PHARMACY_SERVICES,
  SNACK_MESSAGES,
  SNACK_VARIANT,
} from "shared/constants";
import useNotification from "components/atom/snackbar";
import CustomModal from "components/atom/modal";
import TextField from "components/atom/textField/TextField";
import ServiceField from "./ServiceField";
import ActivateDeactivate from "components/molecules/activateDeactivate";

function PharmacyServices() {
  const dispatch = useDispatch();
  const [keyword, setKeyword] = useState("");
  const [msg, sendNotification] = useNotification();
  const [data, setData] = useState([]);
  const [itemsPerPage, setItemsPerPage] = useState(15);
  const [pageCount, setPageCount] = useState(1);
  const [pageItems, setPageItems] = useState(1);
  const [showModal, setShowModal] = useState(false);
  const [modalText, setModalText] = useState(BUTTONS.ADD);
  const [serviceData, setServiceData] = useState({});
  const [multiplePayload, setMultiplePayload] = useState([]);

  const fetchData = useCallback(() => {
    let payload = {
      page: pageCount,
      search: keyword,
      limit: itemsPerPage,
    };

    dispatch(
      pharmacyServiceListing(
        payload,
        (res, type) => {
          setData(res.data);
          setPageItems(res.count)
        },
        (err) => {
          sendNotification({
            msg: err?.data?.message
              ? err.data.message
              : SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
            variant: SNACK_VARIANT.ERROR,
          });
        }
      )
    );
  }, [dispatch, itemsPerPage, keyword, pageCount, sendNotification]);

  const addEditServiceAPI = (e, service, id) => {
    e.preventDefault();
    let payload = {
      service_name: service,
    };

    const onSuccess = (res) => {
      fetchData();
      setShowModal(false);
      sendNotification({
        msg: res?.message ? res.message : SNACK_MESSAGES.DELETED_ITEM,
        variant: SNACK_VARIANT.SUCCESS,
      });
    };

    const onError = (err) => {
      setShowModal(false);
      sendNotification({
        msg: err?.data?.message
          ? err.data.message
          : SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
        variant: SNACK_VARIANT.ERROR,
      });
    };

    if (modalText === BUTTONS.ADD) {
      dispatch(
        pharmacyServiceAdd(payload, (res) => {
          onSuccess(res);
        }),
        (err) => {
          onError(err);
        }
      );
    } else if (modalText === BUTTONS.EDIT) {
      const payloadwithId = {
        data: payload,
        id: serviceData?.id,
      };
      dispatch(
        pharmacyServiceEdit(payloadwithId, (res) => {
          onSuccess(res);
        }),
        (err) => {
          onError(err);
        }
      );
    }
  };

  const statusChange = (id, is_active) => {
    let arr = [];
    if (multiplePayload.length) {
      arr = multiplePayload.map((i, idx) => {
        return {
          id: i,
          status: is_active,
        };
      });
    } else {
      arr = [
        {
          id: id,
          status: is_active,
        },
      ];
    }
    let data = {
      services_status_update: arr,
    };
    dispatch(
      pharmacyServiceStatusChange(
        data,
        (res) => {
          fetchData();
          setMultiplePayload([]);
          sendNotification({
            msg: res?.data?.message
              ? res.data.message
              : SNACK_MESSAGES.SAVED_SUCCESSFULLY,
            variant: SNACK_VARIANT.SUCCESS,
          });
        },
        (err) => {
          sendNotification({
            msg: err?.data?.message
              ? err.data.message
              : SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
            variant: SNACK_VARIANT.ERROR,
          });
        }
      )
    );
  };

  const deletePharmacyService = (id) => {
    let arr = [];
    if (multiplePayload.length) {
      arr = multiplePayload.map((i, idx) => {
        return {
          id: i,
        };
      });
    } else {
      arr = [
        {
          id: id,
        },
      ];
    }

    let data = {
      services: arr,
    };
    dispatch(
      pharmacyServiceDelete(data, (res) => {
        fetchData();
        setMultiplePayload([]);
        sendNotification({
          msg: res?.message ? res.message : SNACK_MESSAGES.DELETED_ITEM,
          variant: SNACK_VARIANT.SUCCESS,
        });
      }),
      (err) => {
        sendNotification({
          msg: err?.data?.message
            ? err.data.message
            : SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
          variant: SNACK_VARIANT.ERROR,
        });
      }
    );
  };

  const ServiceModal = () => {
    return (
      <CustomModal
        isOpen={showModal}
        handleToggle={() => setShowModal((prev) => !prev)}
      >
        <h6>{`${modalText} Service`}</h6>
        <ServiceField
          serviceData={serviceData}
          addEditServiceAPI={addEditServiceAPI}
          buttonText={modalText}
        />
      </CustomModal>
    );
  };

  return (
    <div>
      <div className="d-flex justify-content-md-between">
        <span>{ROUTE_TITLES.PHARMACY_SERVICES}</span>
        <SearchBar
          searchAPI={fetchData}
          keyword={keyword}
          setKeyword={setKeyword}
        />
        {multiplePayload.length ? (
          <>
            <ActivateDeactivate
              statusChange={statusChange}
              title={"Select Action"}
            />
            <button className="btn btn-danger" onClick={() => deletePharmacyService()}>{BUTTONS.DELETE}</button>
          </>
        ) : null}
        <button
          className="btn btn-primary"
          onClick={() => {
            setModalText(BUTTONS.ADD);
            setShowModal(true);
          }}
        >
          {PHARMACY_SERVICES.ADD_SERVICE}
        </button>
      </div>

      <CustomTable>
        <ServicesTable
          data={data}
          statusChange={statusChange}
          setModalText={setModalText}
          setShowModal={setShowModal}
          setServiceData={setServiceData}
          setmultiplePayload={setMultiplePayload}
          multiplepayload={multiplePayload}
          deleteService={deletePharmacyService}
        />
      </CustomTable>

      <Pagination
        items={pageItems}
        setPageCount={setPageCount}
        itemsPerPage={itemsPerPage}
        setItemsPerPage={setItemsPerPage}
      />
      <ServiceModal />
    </div>
  );
}

export default PharmacyServices;
