import React from "react";
import Accordion from "components/atom/Accordion";
import BasicTable from "components/atom/CustomBasicTable";
import ProfileDescription from "components/atom/ProfileDescription";
import ProfileDetails from "components/atom/ProfileDetails";
import { PHARMACY_USER_VIEW, STATUS_OPTIONS } from "shared/constants";
import CustomTable from "components/atom/table";
import { getLocalizedDateString } from "components/utility";

const imageURL =
  "https://images.unsplash.com/photo-1575936123452-b67c3203c357?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxzZWFyY2h8Mnx8aW1hZ2V8ZW58MHx8MHx8&w=1000&q=80";

const tableJobHeader = [
  "#",
  "Name",
  "Position",
  "Start Date",
  "End Date",
  "Description",
];

const tableEducationHeader = [
  "#",
  "Name",
  "Qualification",
  "Start Date",
  "End Date",
  "Description",
];

const tableHeaderFunc = [
  "#",
  "Document Name",
  "Uploaded Document",
  "Status",
  "Action",
];

function Profile({ data, onUnverifyClick, onVerifyClick, onDocumentView }) {
  const data2 = [
    {
      title: "Name",
      value: data?.first_name + " " + data?.last_name,
      isVerified: true,
    },
    {
      title: "Phone",
      value: data?.personal_info?.phone_number
        ? data?.personal_info?.phone_number
        : "",
    },
    {
      title: "DOB",
      value: data?.personal_info?.dob
        ? getLocalizedDateString(data?.personal_info?.dob)
        : "",
    },
    {
      title: "Email Address",
      value: data?.email ? data?.email : "",
      isVerified: data?.verified ? data?.verified : false,
    },
    {
      title: "Address",
      value: data?.address_info ? data?.address_info : "",
    },
    {
      title: "Registration Number",
      value: data?.personal_info?.reg_number
        ? data?.personal_info?.reg_number
        : "",
    },
    {
      title: "Insurance Valid Till",
      value: data?.valid_till ? getLocalizedDateString(data?.valid_till) : "",
    },
  ];

  return (
    <div className="tab_content">
      <ProfileDetails
        data={data2}
        imageURL={
          data?.personal_info?.photo ? data?.personal_info?.photo : imageURL
        }
        rating={3}
      />
      <ProfileDescription
        title={"Descriptions"}
        description={
          data?.personal_info?.personal_description
            ? data?.personal_info?.personal_description
            : ""
        }
      />

      <section className="accordin_sec">
        <Accordion title={"Education"}>
          {data?.edu_history?.length ? (
            <div className="customTable">
              <CustomTable>
                <thead>
                  <tr key={1000 + 1}>
                    {tableEducationHeader.map((i, idx) => (
                      <th scope="col" id={idx}>
                        {i}
                      </th>
                    ))}
                  </tr>
                </thead>
                <tbody>
                  {data?.edu_history?.length
                    ? data?.edu_history?.map((i, idx) => {
                      return (
                        <>
                          <tr>
                            <td>{idx + 1} </td>
                            <td>{i?.institution_name} </td>
                            <td>{i?.qualification} </td>
                            <td>{i?.start_date} </td>
                            <td>{i?.end_date} </td>
                            <td>{i?.description} </td>
                          </tr>
                        </>
                      );
                    })
                    : <tr><td colSpan={6} className="text-center py-5">No Documents Uploaded</td></tr>
                  }
                </tbody>
              </CustomTable>
              {/* <BasicTable
                    theadData={tableHeader}
                    tbodyData={
                      data?.edu_history?.length ? data?.edu_history : []
                    }
                  /> */}
            </div>
          ) : (
            <span>{"No Details Uploaded"} </span>
          )}
        </Accordion>
        <Accordion title={"Experience"}>
          {data?.job_history?.length ? (
            <div className="customTable">
              <CustomTable>
                <thead>
                  <tr key={1000 + 1}>
                    {tableJobHeader.map((i, idx) => (
                      <th scope="col" id={idx}>
                        {i}
                      </th>
                    ))}
                  </tr>
                </thead>
                <tbody>
                  {data?.job_history?.length
                    ? data?.job_history?.map((i, idx) => {
                      return (
                        <>
                          <tr>
                            <td>{idx + 1} </td>
                            <td>{i?.company_name} </td>
                            <td>{i?.job_title} </td>
                            <td>{i?.start_date} </td>
                            <td>{i?.end_date} </td>
                            <td>{i?.description} </td>
                          </tr>
                        </>
                      );
                    })
                    : <tr><td colSpan={6} className="text-center py-5">No Documents Uploaded</td></tr>
                  }
                </tbody>
              </CustomTable>
            </div>
          ) : (
            <span>{"No Details Uploaded"} </span>
          )}
        </Accordion>
      </section>

      <div className="card">
        <div className="card-body">
          <h2 className="h6 text-captialize">Uploaded Documents</h2>

          {data?.documents?.length ? (

            <CustomTable>
              <thead>
                <tr key={1000 + 1}>
                  {tableHeaderFunc.map((i, idx) => (
                    <th scope="col" id={idx}>
                      {i}
                    </th>
                  ))}
                </tr>
              </thead>
              <tbody>
                {data?.documents?.length
                  ? data?.documents?.map((i, idx) => {
                    const documentType = i.document_type;
                    const title =
                      PHARMACY_USER_VIEW.DOCUMENT_TYPE_DATA[
                        documentType
                      ]?.title;
                    console.log(
                      PHARMACY_USER_VIEW.DOCUMENT_TYPE_DATA,
                      i.document_type
                    );
                    return (
                      <>
                        <tr>
                          <td>{idx + 1} </td>
                          <td>{title ? title : ""} </td>
                          <td
                            onClick={() => {
                              onDocumentView(
                                i?.id,
                                documentType,
                                data?.documents
                              );
                            }}
                          >
                            {i?.document ? i?.name : "Not Uploaded"}
                          </td>
                          <td>
                            <button
                              className="btn btn-secondary"
                              onClick={() =>
                                onVerifyClick(documentType)
                              }
                              disabled={
                                i?.verified ===
                                STATUS_OPTIONS.VERIFIED.key
                              }
                            >
                              {STATUS_OPTIONS.VERIFIED.title}
                            </button>
                            <button
                              className="btn btn-primary"
                              onClick={() => {
                                onUnverifyClick(
                                  i?.id,
                                  documentType,
                                  data?.documents
                                );
                              }}
                              disabled={
                                i?.verified ===
                                STATUS_OPTIONS.UNVERIFIED.key
                              }
                            >
                              {STATUS_OPTIONS.UNVERIFIED.title}
                            </button>
                          </td>
                        </tr>
                      </>
                    );
                  })
                  : <tr><td colSpan={5} className="text-center py-5">No Documents Uploaded</td></tr>
                }
              </tbody>
            </CustomTable>

          ) : (
            <span>{"No Details Uploaded"} </span>
          )}
        </div>
      </div>
    </div>
  );
}

export default Profile;
