import ButtonStatus from "components/atom/button/ButtonStatus";
import { Pagination } from "components/atom/pagination";
import CustomTable from "components/atom/table";
import { splice } from "components/utility";
import React, { useState } from "react";
import { SORT_KEY } from "shared/constants";
import { Images } from "shared/images";
import { BRANCH_SERVICES } from "shared/tableData";

function BranchesTable({
  setmultiplePayload,
  multiplepayload,
  navigateBranches = () => { },
  data,
  deleteBranch = () => { },
  itemsPerPage,
  setItemsPerPage,
  pageCount,
  setPageCount,
  setSortKey,
  sortKey,
}) {
  const sortSwitchKey = (name) => {
    switch (name) {
      case BRANCH_SERVICES.BRANCH_NAME:
        return setSortKey(
          sortKey === SORT_KEY.branch_name
            ? SORT_KEY.default
            : SORT_KEY.branch_name
        );
      default:
        return setSortKey(SORT_KEY.default);
    }
  };

  return (
    <div className="tab_content">

      <CustomTable>
        <thead>
          <tr key={1000 + 1}>
            <th>
              <input
                type="checkbox"
                disabled={!multiplepayload.length}
                checked={multiplepayload.length}
                onChange={(e) => {
                  if (multiplepayload.length) {
                    setmultiplePayload([]);
                  }
                }}
              />
            </th>
            {Object.values(BRANCH_SERVICES).map((i, idx) => (
              <th scope="col" id={idx} onClick={() => sortSwitchKey(i)}>
                {i}
              </th>
            ))}
          </tr>
        </thead>
        {data.length
          ? data.map((i, idx) => {
            return (
              <>
                <tbody>
                  <tr>
                    <td>
                      <input
                        type="checkbox"
                        checked={multiplepayload.includes(i?.id)}
                        onChange={(e) => {
                          if (e.target.checked) {
                            setmultiplePayload((prev) => [...prev, i?.id]);
                          } else {
                            setmultiplePayload(
                              splice(multiplepayload, i?.id)
                            );
                          }
                        }}
                      />
                    </td>
                    <td>{idx + 1} </td>
                    <td>{i?.branch_name} </td>
                    <td>{i?.phone_number} </td>
                    <td>{i?.number_of_staff} </td>
                    <td>
                      <div className="d-flex">
                        <ButtonStatus
                          image={Images.viewIcon}
                          status={"default"}
                          title={"View Details"}
                          onClick={() => navigateBranches(i?.id)}
                        />
                        <ButtonStatus
                          image={Images.deleteIcon}
                          status={"Unverified"}
                          title={"View Details"}
                          onClick={() => deleteBranch(i?.id)}
                        />
                      </div>
                    </td>
                  </tr>
                </tbody>
              </>
            );
          })
          : <tbody><tr><td colSpan={6} className="text-center py-5">No Documents Uploaded</td></tr></tbody>
        }

      </CustomTable>

      <Pagination
        items={data.length ? data : []}
        setPageCount={setPageCount}
        itemsPerPage={itemsPerPage}
        setItemsPerPage={setItemsPerPage}
      />
    </div>
  );
}

export default BranchesTable;
