import ButtonGreen from "components/atom/button/ButtonGreen";
import CustomModal from "components/atom/modal";
import TextField from "components/atom/textField/TextField";
import React, { useState } from "react";
import { STATUS_OPTIONS, STRINGS } from "shared/constants";

function DocUnverifyModal({
  setShow,
  show,
  onClick = () => { },
  reason,
  setReason,
}) {
  const disabled = () => {
    if (!show?.length) {
      return true;
    } else {
      return false;
    }
  };
  return (
    <>
      <CustomModal
        isOpen={show}
        className="as"
        handleToggle={() => setShow((prev) => !prev)}
      >
        <div className="text-center">
          <h2 className="h5 mb-2">{STRINGS.REASON_TO_UNVERIFY}</h2>

          <div className="mb-4">
            <label label={STRINGS.ENTER_REASON} />
            <textarea
              placeholder={"Enter here..."}
              name="unverify"
              className="form-control"
              rows="4"
              value={reason}
              onChange={(e) => setReason(e.target.value)}
            ></textarea>
          </div>

          <ButtonGreen
            title={STATUS_OPTIONS.UNVERIFIED.title}
            onClick={() => onClick()}
            disabled={disabled()}
            className="btn btn-md btn-primary"
          />

        </div>
      </CustomModal >
    </>
  );
}

export default DocUnverifyModal;
