import React from "react";
import Select from "react-select";

const customStyles = {
  option: (provided, state) => ({
    ...provided,
    backgroundColor: state.isSelected ? "red" : "pink",
    color: state.isSelected ? "pink" : "black",
  }),
};
  
function ReactSelect({ selectedOption, handleChange, options, title }) {
  return (
    <>
      <Select
        value={selectedOption}
        onChange={handleChange}
        options={options}
        isClearable={false}
        placeholder={title}
        classNamePrefix="react-prefix"
        className={`react-select ${selectedOption.color}`}
        styles={customStyles}
      />
    </>
  );
}

export default ReactSelect;
